.contact {
    background-color: rgb(255, 255, 255);
    color: rgb(38, 38, 38);
    transition: box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    overflow: hidden;
    border: 1px solid rgb(230, 235, 241);
    border-radius: 8px;
    box-shadow: inherit;
    padding: 1rem;
    margin-top: 1rem;
    margin-bottom: 1rem;
    margin-left: 1rem;
    margin-right: 1rem;
    flex-grow: 1;
    overflow: "hidden";
    padding-bottom: 2rem;
}
.displaycontent{
    display: flex;
    justify-content: space-between;
}
.contactform{
max-width: 500px;
padding: 10px;
}
.contactus{
    font-size: 22px;
    line-height: 24px;
    font-weight: 600;
    text-rendering: optimizeLegibility;
    margin-bottom: 0!important;
    text-rendering: optimizeLegibility;
    display: block;
    font-family: "Amazon Ember",Arial,sans-serif;
    color: #0F1111;
    padding: 5px;
    background-color: green;
    color: white;
    width: 150px;
    text-align: center;
}
.aboutheading{
    text-align: left;
    color: #232F3E;
    margin-left: 10px;
    font-family: AmazonEmberBold,Helvetica Neue Bold,Helvetica Neue,Helvetica,Arial,sans-serif;
    font-weight: 500;  
    font-size: 2.2rem;
    word-wrap: break-word;
    margin-bottom: 5px;
  }